  name: "app",
  components: { },
  setup() {
    // const { t, te } = useI18n();
    const store = useStore();

    onMounted(() => {
      /**
       * this is to override the layout config using saved data from localStorage
       * remove this to use config only from static config (@/core/config/DefaultLayoutConfig.ts)
